body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
main{
  margin: 15px auto;
  width: fit-content;
  background:#CADBE9;
  border: #D0D0D0 0.5px solid;
  padding: 10px;
}
.title{
  text-align:center;
  font-size:25px; 
}
input{
  padding:10px;
  margin:10px 0;
  border:0.5px gray solid;
  border-radius:3px;
  width:-webkit-fill-available;
}
ul{
  margin: 5px 0;
  padding: 0;
}
li{
  list-style-type: circle;
  list-style-position: inside;
  color: #0a406b;
  background: rgba(142, 192, 228, 0.795);
  padding:5px 10px;
  border-radius: 1px;
  border-bottom: 1px;
  margin-bottom: 5px;
}
.hidden{
  display: none;
}
li > button{
  color: #d68189;
  background: #fcdee2;
  transition: color .15s ease-in-out,background-color .15s ease-in-out;
  border:1px solid transparent;
  border-radius: 3px;
  margin-left: 5px;
}
.complete > .changeType{
  color: #55967e;
  background-color: #9BD5BD;
}
.delete{
  color:#52616a;
  background-color: #a9b4c7;
  transition: color .15s ease-in-out,background-color .15s ease-in-out;
}
.changeType:hover{
  background-color: #F6B6CA;
}
.complete > .changeType:hover{
  color: #DAEBEB;
  background-color: #689D79;
}
.delete:hover{
  color: #e3dede;
  background-color: #8291ab;
}
span > button{
  color: #77919d;
  background: #b6c6d4;
  transition: color .15s ease-in-out,background-color .15s ease-in-out;
  border:1px solid transparent;
  margin-right: 5px;
}
span > button:hover{
  color: #577786;
  background-color: #97a6b4;
}
.present{
  background-color: rgba(108, 161, 199, 0.795);
  color: #3d5b69;
}